export class Services {
    getItems() {
        return new Promise((resolve, reject) => {
            let url = "http://zoosvc.com/service.svc/getItems";
            fetch(url, {
                method: 'GET',
                headers: {
                    'Accept': 'application/json; odata=verbose',
                    'Content-type': 'application/json',
                }
            }).then((data) => {
                resolve(data);
            }).catch((err) => {
                reject(err);
            });
        });
    } //getItems
}
