export class IFormField {
    constructor() {
        this.disabled = true;
        this.error = '',
            this.internalName = '',
            this.required = false;
    }
    haveError() {
        return this.error.length > 0;
    }
    changeDisabledValueByConfig(fields) {
        this.disabled = fields.indexOf(this.internalName) == -1;
    }
}
